import { Col, Grid, Row } from 'antd';
import { FC, useMemo } from 'react';
import HeadingText from './HeadingText/HeadingText';
import style from './Home.module.less';
import SkillCards from './SkillCards/SkillCards';


const Home: FC = () => {

	const { useBreakpoint } = Grid;
	const breakpoints = useBreakpoint()

	const card_col_span: number = useMemo(() => {
		const screens = Object.entries(breakpoints).filter(screen => !!screen[1])
		const screen_size = screens[screens.length - 1]?.[0]
		switch (screen_size) {
			case 'md': return 24 / 2
			case 'sm': return 24 / 1
			case 'xs': return 24 / 1
			default: return 24 / 3
		}
	}, [breakpoints])


	return (
		<Row align={'middle'} justify={'center'} className={style.body}>
			<Col span={24} className={style.home_description_row_columns}>
				<Row align={'middle'} justify={'center'} >
					<Col>
						<HeadingText />
					</Col>
				</Row>
			</Col>
			<Col span={20}>
				<SkillCards cardSpan={card_col_span} />
			</Col>
		</Row>
	);
}

export default Home;