interface DevCardObject {
  title: string,
  description: string,
  image_url: string;
  techonologiesAndTools?: string[];
};

class DevCardClass {
  title: string;
  description: string;
  image_url: string;
  techonologiesAndTools?: string[];

  constructor(title: string, description: string, image: string, techonologiesAndTools?: string[]) {
    this.title = title;
    this.description = description;
    this.image_url = image
    this.techonologiesAndTools = techonologiesAndTools;
  }
};

export {DevCardClass}
export type { DevCardObject };
