import { Col, Drawer, Row } from "antd";
import { FC, useState } from "react";
import { DevCardClass, DevCardObject } from "../../../classesAndInterfaces/DevCard";
import DevCard from "../../../components/DevCard/DevCard";
import { awsCardDescription, awsTechnologiesAndTools } from "../../../textFiles/aws";
import { backendCardDescription, backendTechnologiesAndTools } from "../../../textFiles/backend";
import { frontendCardDescription, frontendTechnologiesAndTools } from "../../../textFiles/frontend";


const devCardArray: DevCardObject[] = [
	new DevCardClass('frontend', frontendCardDescription, '/img/frontend.png', frontendTechnologiesAndTools),
	new DevCardClass('aws', awsCardDescription, '/img/aws.png', awsTechnologiesAndTools),
	new DevCardClass('backend', backendCardDescription, '/img/node.jpg', backendTechnologiesAndTools),
]


const SkillCards: FC<{cardSpan: number}> = ({cardSpan}) => {
	const [drawer, setDrawer] = useState(false)

	const openDrawer = () => {
		setDrawer(true)
	}
	

	return (
			<Row key={'demo'} align={'middle'} justify={'center'} gutter={[24, 24]}>
				{
					devCardArray.map((card: DevCardObject, index: number) => {
						return (
							<Col key={index} span={cardSpan}>
									<DevCard title={card.title} description={card.description} image_url={card.image_url} techonologiesAndTools={card.techonologiesAndTools} openDrawer={openDrawer}/>
							</Col>
						)
					})
				}
				{/* <Drawer
          title="Multi-level drawer"
          width={'100%'}
          closable={false}
          onClose={()=>{setDrawer(false)}}
          visible={drawer}
        ></Drawer> */}
			</Row>
	)
}

export default SkillCards;